
//Анимация которая поднимает кнопку на 5px вверх.

@keyframes BtnToUp {
    0% {
        opacity: 0;
        transform: translateY(5rem); }

    100% {
        opacity: 1;
        transform: translateY(0); } }

//Анимая которая двигает текст слева направо

@keyframes leftToRight {
    0% {
        opacity: 0;
        transform: translateX(-10rem); }
    80% {
        transform: translateX(1rem); }

    100% {
        opacity: 1;
        transform: translateX(0); } }

//Анимая которая двигает текст справа налево

@keyframes rightToLeft {
    0% {
        opacity: 0;
        transform: translateX(10rem); }
    80% {
        transform: translateX(-1rem); }

    100% {
        opacity: 1;
        transform: translateX(0); } }
